import React, { useCallback, useEffect, useMemo, useState } from 'react';
import classNames from 'classnames';
import { useDebounce } from 'use-debounce';
import {
  Button, Container, Divider, Form, Grid, Header, Label, Loader, Modal, TextArea
} from 'semantic-ui-react';

import withAuth from './with-auth';
import useField from './use-field';

import styles from './App.module.css';

import 'semantic-ui-css/semantic.min.css';

const FORMATS_MAP = [
  ['anagrams', 'Анаграммы'],
  ['plusograms', 'Плюсограммы'],
  ['metagrams', 'Метаграммы'],
  ['metanagrams', 'Метанаграммы'],
  ['tlenograms', 'Тленограммы'],
  ['logogrifs', 'Логогрифы'],
  ['logogrifs2', 'Логогрифы +2'],
  ['metagrifs', 'Метагрифы'],
  ['brukvas', 'Брюквы'],
  ['tikvas', 'Тыквы'],
  ['hybrids3', 'Гибриды (3)'],
  ['hybrids4', 'Гибриды (4)'],
  ['sharadas', 'Шарады'],
  ['golovahvosts', 'Головахвосты'],
];

const DICTIONARIES_MAP = [
  ['sanstv', 'sanstv.ru'],
  ['sociation', 'sociation.org'],
];

const isAnagram = (word1, word2) => {
  if (word1.length !== word2.length) {
    return false;
  }

  const word1Sorted = word1.split('').sort().join('');
  const word2Sorted = word2.split('').sort().join('');

  return word1Sorted === word2Sorted;
}

const isPlusogram = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  const word1Sorted = word1.split('').sort().join('');
  const word2Sorted = word2.split('').sort().join('');

  let hasDifference = false;
  for (let i = 0; i < word2Sorted.length; i++) {
    if (word1Sorted.charAt(i - +hasDifference) !== word2Sorted.charAt(i)) {
      if (hasDifference) {
        return false;
      }

      hasDifference = true;
    }
  }

  return hasDifference;
}

const isMetagram = (word1, word2) => {
  if (word1.length !== word2.length) {
    return false;
  }

  let hasDifference = false;
  for (let i = 0; i < word1.length; i++) {
    if (word1.charAt(i) !== word2.charAt(i)) {
      if (hasDifference) {
        return false;
      }

      hasDifference = true;
    }
  }

  return hasDifference;
}

const isMetanagram = (word1, word2) => {
  if (word1.length !== word2.length) {
    return false;
  }

  for (let i = 0; i < word1.length; i++) {
    word2 = word2.replace(word1.charAt(i), '');
  }

  return word2.length === 1;
}

const isTlenogram = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  for (let i = 0; i < word1.length; i++) {
    word2 = word2.replace(word1.charAt(i), '');
  }

  return word2.length === 2;
}

const isLogogrif = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  let hasDifference = false;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +hasDifference) !== word2.charAt(i)) {
      if (hasDifference) {
        return false;
      }

      hasDifference = true;
    }
  }

  return hasDifference;
}

const isLogogrif2 = (word1, word2) => {
  if (word2.length - word1.length !== 2) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - totalDifferences) === word2.charAt(i)) {
      if (totalDifferences === 1) {
        return false;
      }
    } else {
      if (totalDifferences === 2) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 2;
}

const isMetagrif = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +!!totalDifferences) !== word2.charAt(i)) {
      totalDifferences++;

      if (totalDifferences > 2) {
        break;
      }
    }
  }

  if (totalDifferences === 2) {
    return true;
  }

  totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +(totalDifferences === 2)) !== word2.charAt(i)) {
      if (totalDifferences === 2) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 2;
}

const isBrukva = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +!!totalDifferences) === word2.charAt(i)) {
      if (totalDifferences === 1) {
        return false;
      }
    } else {
      if (totalDifferences === 2) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 2;
}

const isTikva = (word1, word2) => {
  if (word2.length - word1.length !== 1) {
    return false;
  }

  let totalDifferences = 0;
  for (let i = 0; i < word2.length; i++) {
    if (word1.charAt(i - +!!totalDifferences) === word2.charAt(i)) {
      if (totalDifferences === 1 || totalDifferences === 2) {
        return false;
      }
    } else {
      if (totalDifferences === 3) {
        return false;
      }

      totalDifferences++;
    }
  }

  return totalDifferences === 3;
}

const createHybrid3 = (word1, word2) => {
  const intersection = word1.slice(-3);
  if (word2.slice(0, 3) === intersection) {
    return word1.slice(0, -3) + intersection.toUpperCase() + word2.slice(3);
  }

  return null;
}

const createHybrid4 = (word1, word2) => {
  const intersection = word1.slice(-4);
  if (word2.slice(0, 4) === intersection) {
    return word1.slice(0, -4) + intersection.toUpperCase() + word2.slice(4);
  }

  return null;
}

const createSharada = (word1, word2, dictionarySet) => {
  const word = word1 + word2;
  if (dictionarySet.has(word)) {
    return word;
  }

  return null;
}

const createGolovahvosts = (word1, word2, dictionarySet) => {
  if (word1.length < 2 || word2.length < 2) {
    return [];
  }

  const golovahvosts = [];
  for (let i = 1; i < word1.length; i++) {
    const word1Tail = word1.slice(i);

    for (let j = 1; j < word2.length; j++) {
      const word = word1Tail + word2.slice(0, -j);
      if (word.length > 3 && dictionarySet.has(word)) {
        golovahvosts.push(word1.slice(0, i) + word.toUpperCase() + word2.slice(-j));
      }
    }
  }

  return golovahvosts;
};

const getFormats = (words, dictionarySet, selectedFormatsSet) => {
  const formats = FORMATS_MAP.reduce((result, [format]) => {
    if (selectedFormatsSet.has(format)) {
      return {
        ...result,
        [format]: [],
      }
    }

    return result;
  }, {});

  words.forEach((word1, i) => {
    words.forEach((word2, j) => {
      if (j === i) {
        return;
      }

      if (j > i) {
        if (formats.anagrams && isAnagram(word1, word2)) {
          formats.anagrams.push(`${word1} ${word2}`);
        }

        if (formats.metagrams && isMetagram(word1, word2)) {
          formats.metagrams.push(`${word1} ${word2}`);
        }

        if (formats.metanagrams && isMetanagram(word1, word2)) {
          formats.metanagrams.push(`${word1} ${word2}`);
        }
      }

      if (formats.plusograms && isPlusogram(word1, word2)) {
        formats.plusograms.push(`${word1} ${word2}`);
      }

      if (formats.tlenograms && isTlenogram(word1, word2)) {
        formats.tlenograms.push(`${word1} ${word2}`);
      }

      if (formats.logogrifs && isLogogrif(word1, word2)) {
        formats.logogrifs.push(`${word1} ${word2}`);
      }

      if (formats.logogrifs2 && isLogogrif2(word1, word2)) {
        formats.logogrifs2.push(`${word1} ${word2}`);
      }

      if (formats.metagrifs && isMetagrif(word1, word2)) {
        formats.metagrifs.push(`${word1} ${word2}`);
      }

      if (formats.brukvas && isBrukva(word1, word2)) {
        formats.brukvas.push(`${word1} ${word2}`);
      }

      if (formats.tikvas && isTikva(word1, word2)) {
        formats.tikvas.push(`${word1} ${word2}`);
      }

      if (formats.hybrids3) {
        const hybrid3 = createHybrid3(word1, word2);
        if (hybrid3) {
          formats.hybrids3.push(hybrid3);
        }
      }

      if (formats.hybrids4) {
        const hybrid4 = createHybrid4(word1, word2);
        if (hybrid4) {
          formats.hybrids4.push(hybrid4);
        }
      }

      if (formats.sharadas) {
        const sharada = createSharada(word1, word2, dictionarySet);
        if (sharada) {
          formats.sharadas.push(sharada);
        }
      }

      if (formats.golovahvosts) {
        formats.golovahvosts.push(...createGolovahvosts(word1, word2, dictionarySet));
      }
    });
  });

  return formats;
}

const App = () => {
  const [database, setDatabase] = useState(null);

  const [selectedFormatsSet, setSelectedFormatsSet] = useState(
    new Set(FORMATS_MAP.map(([format]) => format)),
  );

  const [selectedDictionary, handleSelectedDictionaryChange] = useField('sanstv');
  const [words, handleWordsChange] = useField('');
  const [wordsDebounced] = useDebounce(words, 500);

  const wordsClean = useMemo(
    () =>
      wordsDebounced
        .trim()
        .toLowerCase()
        .replace('ё', 'е')
        .split(/\s+/)
        .filter((word, index, self) => {
          return index === self.indexOf(word);
        }),
    [wordsDebounced],
  );

  const formats = useMemo(() => {
    if (!database) {
      return {};
    }

    return getFormats(wordsClean, database[selectedDictionary].set, selectedFormatsSet);
  }, [database, selectedFormatsSet, selectedDictionary, wordsClean]);

  const handleSelectedFormatChange = useCallback((_, { value }) => {
    if (selectedFormatsSet.has(value)) {
      setSelectedFormatsSet(new Set([...selectedFormatsSet].filter(format => format !== value)));
    } else {
      setSelectedFormatsSet(new Set([...selectedFormatsSet, value]));
    }
  }, [selectedFormatsSet]);

  useEffect(() => {
    (async () => {
      const { sanstv, sociation } = await import('./database.json');

      setDatabase({
        sanstv: {
          array: sanstv,
          set: new Set(sanstv),
        },
        sociation: {
          array: sociation,
          set: new Set(sociation),
        },
      });
    })();
  }, []);

  if (!database) {
    return <Loader active>Загружаю базу...</Loader>;
  }

  return (
    <>
      <div className={styles.header}>
        <Container>
          <Grid padded>
            <Grid.Row>
              <Grid.Column width={15}>
                <Form>
                  <TextArea
                    autoFocus
                    value={words}
                    placeholder="Слова"
                    onChange={handleWordsChange}
                  />
                </Form>
              </Grid.Column>
              <Grid.Column>
                <Modal size="mini" trigger={<Button size="big" icon="setting" />}>
                  <Header icon="setting" content="Настройки" />
                  <Modal.Content>
                    <Form>
                      <Form.Group grouped>
                        <label>Форматы</label>
                        {FORMATS_MAP.map(([format, formatLabel]) => (
                          <Form.Checkbox
                            key={format}
                            value={format}
                            label={formatLabel}
                            checked={selectedFormatsSet.has(format)}
                            onChange={handleSelectedFormatChange}
                          />
                        ))}
                      </Form.Group>
                      <Form.Group grouped>
                        <label>Словарь</label>
                        {DICTIONARIES_MAP.map(([dictionary, dictionaryLabel]) => (
                          <Form.Radio
                            key={dictionary}
                            value={dictionary}
                            label={dictionaryLabel}
                            checked={dictionary === selectedDictionary}
                            onChange={handleSelectedDictionaryChange}
                          />
                        ))}
                      </Form.Group>
                    </Form>
                  </Modal.Content>
                </Modal>
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </Container>
      </div>
      <div className={styles.content}>
        <Container>
          <Grid padded>
            {FORMATS_MAP.filter(([format]) => formats[format] && formats[format].length > 0).map(
              ([format, formatLabel]) => (
                <Grid.Row key={format}>
                  <Grid.Column>
                    <Divider horizontal>
                      <Header as="h4" className={styles.title}>
                        {formatLabel}
                      </Header>
                    </Divider>
                    <div className={styles.words}>
                      {formats[format].map((word, i) => (
                        <Label
                          horizontal
                          key={word}
                          className={classNames(styles.word, i % 2 && styles.odd)}
                        >
                          {word}
                        </Label>
                      ))}
                    </div>
                  </Grid.Column>
                </Grid.Row>
              ),
            )}
          </Grid>
        </Container>
      </div>
    </>
  );
};

export default withAuth(App);
